.day-tabs-wrap {
  overflow-x: auto;
  padding-bottom: 4px;
}

.day-tabs {
  display: flex;
  gap: 6px;
  min-width: max-content;
}

.day-tab {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 5px;
  padding: 10px 20px 8px;
  border-radius: 12px;
  border: 1.5px solid var(--border);
  background: var(--card);
  color: var(--muted);
  font-size: 13px;
  font-weight: 500;
  min-width: 80px;
  transition: all 0.18s ease;
  position: relative;
}

.day-tab:hover:not(.active) {
  border-color: var(--accent);
  color: var(--accent);
  background: #FFF5EF;
}

.day-tab.active {
  background: var(--brown);
  border-color: var(--brown);
  color: var(--cream);
}

.day-tab.over {
  border-color: var(--red) !important;
}

.tab-short { display: none; font-weight: 700; font-size: 14px; }
.tab-full  { font-size: 12px; }

.tab-progress-wrap {
  width: 40px;
  height: 3px;
  background: var(--border);
  border-radius: 2px;
  overflow: hidden;
}

.tab-progress-bar {
  height: 100%;
  border-radius: 2px;
  transition: width 0.4s ease;
}

.tab-empty-dot {
  width: 5px;
  height: 5px;
  border-radius: 50%;
  background: var(--border);
}

.day-tab.active .tab-empty-dot {
  background: rgba(255,255,255,0.3);
}
.day-tab.active .tab-progress-wrap {
  background: rgba(255,255,255,0.15);
}

@media (max-width: 700px) {
  .tab-short { display: block; }
  .tab-full  { display: none; }
  .day-tab   { min-width: 52px; padding: 8px 10px 6px; }
}