.template-modal {
  width: 500px;
  max-height: 86vh;
  display: flex;
  flex-direction: column;
  gap: 0;
  overflow: hidden;
}

.tpl-head {
  padding: 24px 24px 16px;
  border-bottom: 1px solid var(--border);
}

.tpl-head h2 {
  font-family: var(--font-display);
  font-size: 22px;
  margin-bottom: 4px;
}

.tpl-head p {
  font-size: 13px;
  color: var(--muted);
}

/* Save section */
.tpl-save-section {
  padding: 16px 24px;
  background: var(--warm);
  border-bottom: 1px solid var(--border);
}

.tpl-save-label {
  font-size: 13px;
  color: var(--brown2);
  margin-bottom: 10px;
}

.tpl-save-row {
  display: flex;
  gap: 10px;
}

.tpl-input {
  flex: 1;
  padding: 9px 14px;
  border-radius: 8px;
  border: 1.5px solid var(--border);
  font-size: 13px;
  background: var(--card);
  color: var(--brown);
  outline: none;
  font-family: var(--font-body);
  transition: border-color 0.2s;
}
.tpl-input:focus { border-color: var(--accent); }

/* List */
.tpl-list {
  flex: 1;
  overflow-y: auto;
  padding: 12px 16px;
  display: flex;
  flex-direction: column;
  gap: 8px;
}

.tpl-empty {
  text-align: center;
  padding: 32px 16px;
  color: var(--muted);
  font-size: 13px;
  line-height: 1.8;
}

.tpl-item {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 12px;
  padding: 12px 14px;
  background: var(--cream);
  border-radius: 10px;
  border: 1.5px solid var(--border);
  transition: border-color 0.15s;
}

.tpl-item:hover { border-color: var(--accent); }

.tpl-item-info { flex: 1; min-width: 0; }

.tpl-item-name {
  font-size: 14px;
  font-weight: 700;
  color: var(--brown);
  margin-bottom: 3px;
}

.tpl-item-meta {
  font-size: 11px;
  color: var(--muted);
}

.tpl-item-actions {
  display: flex;
  gap: 6px;
  flex-shrink: 0;
}

/* Footer */
.tpl-footer {
  padding: 14px 24px;
  border-top: 1px solid var(--border);
  display: flex;
  justify-content: flex-end;
}