.food-modal {
  width: 520px;
  max-height: 88vh;
  display: flex;
  flex-direction: column;
}

.food-modal-head {
  padding: 22px 22px 0;
  display: flex;
  flex-direction: column;
  gap: 12px;
  border-bottom: 1px solid var(--border);
  padding-bottom: 14px;
}

.food-modal-title {
  font-family: var(--font-display);
  font-size: 20px;
  color: var(--brown);
  display: flex;
  align-items: center;
  gap: 10px;
}

.food-modal-meal {
  font-family: var(--font-body);
  font-size: 12px;
  font-weight: 600;
  background: var(--warm);
  color: var(--brown2);
  padding: 3px 10px;
  border-radius: 12px;
}

.food-modal-search-row {
  position: relative;
}

.food-modal-input {
  width: 100%;
  padding: 10px 36px 10px 14px;
  border-radius: 10px;
  border: 1.5px solid var(--border);
  font-size: 14px;
  color: var(--brown);
  background: var(--cream);
  outline: none;
  transition: border-color 0.2s;
}

.food-modal-input:focus {
  border-color: var(--accent);
}

.food-modal-clear {
  position: absolute;
  right: 10px;
  top: 50%;
  transform: translateY(-50%);
  background: none;
  border: none;
  font-size: 18px;
  color: var(--muted);
  cursor: pointer;
  line-height: 1;
}
.food-modal-clear:hover { color: var(--brown); }

/* Category tabs */
.food-modal-cats {
  display: flex;
  gap: 6px;
  overflow-x: auto;
}

.cat-tab {
  padding: 5px 14px;
  border-radius: 16px;
  border: 1.5px solid var(--border);
  background: none;
  font-size: 12px;
  font-weight: 500;
  color: var(--muted);
  white-space: nowrap;
  transition: all 0.15s;
}
.cat-tab:hover  { border-color: var(--accent); color: var(--accent); }
.cat-tab.active { background: var(--brown); border-color: var(--brown); color: var(--cream); }

/* Quick filters */
.food-modal-filters {
  display: flex;
  gap: 6px;
}

.filter-chip {
  padding: 4px 12px;
  border-radius: 14px;
  border: 1px solid var(--border);
  background: none;
  font-size: 12px;
  color: var(--muted);
  transition: all 0.15s;
}
.filter-chip:hover  { border-color: var(--accent); color: var(--accent); }
.filter-chip.active { background: var(--accent); border-color: var(--accent); color: white; }

/* Body */
.food-modal-body {
  overflow-y: auto;
  flex: 1;
  padding: 10px 12px;
  display: flex;
  flex-direction: column;
  gap: 4px;
}

.food-modal-empty {
  text-align: center;
  padding: 32px;
  color: var(--muted);
  font-size: 14px;
}

.food-result {
  display: flex;
  align-items: center;
  gap: 12px;
  padding: 10px 12px;
  border-radius: 12px;
  cursor: pointer;
  border: 1.5px solid transparent;
  transition: all 0.15s;
}

.food-result:hover {
  background: var(--warm);
  border-color: var(--border);
  transform: translateX(2px);
}

.food-result-icon {
  font-size: 24px;
  width: 40px;
  height: 40px;
  display: flex;
  align-items: center;
  justify-content: center;
  background: var(--cream);
  border-radius: 10px;
  flex-shrink: 0;
  border: 1px solid var(--border);
}

.food-result-info { flex: 1; min-width: 0; }

.food-result-name {
  font-size: 14px;
  font-weight: 600;
  color: var(--brown);
  margin-bottom: 4px;
}

.food-result-chips {
  display: flex;
  gap: 5px;
  flex-wrap: wrap;
}

.food-result-kcal {
  font-size: 14px;
  font-weight: 700;
  color: var(--accent);
  white-space: nowrap;
}

/* Footer */
.food-modal-footer {
  padding: 12px 22px;
  border-top: 1px solid var(--border);
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.food-modal-count {
  font-size: 12px;
  color: var(--muted);
}