.meal-card {
  background: var(--card);
  border: 1.5px solid var(--border);
  border-radius: 16px;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  transition: box-shadow 0.2s, transform 0.2s;
}

.meal-card:hover {
  box-shadow: 0 6px 24px var(--shadow);
  transform: translateY(-2px);
}

.meal-card-header {
  padding: 12px 16px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  border-bottom: 1px solid var(--border);
}

.meal-card-header-left {
  display: flex;
  align-items: center;
  gap: 8px;
}

.meal-card-emoji { font-size: 20px; }

.meal-card-label {
  font-size: 14px;
  font-weight: 700;
  color: var(--brown);
  letter-spacing: 0.2px;
}

.meal-kcal-badge {
  font-size: 13px;
  font-weight: 700;
  padding: 3px 10px;
  background: rgba(255,255,255,0.7);
  border-radius: 12px;
}

.meal-card-body {
  padding: 12px 14px 14px;
  display: flex;
  flex-direction: column;
  gap: 6px;
  flex: 1;
}

.meal-empty {
  font-size: 12px;
  color: var(--muted);
  text-align: center;
  padding: 12px 0 8px;
  font-style: italic;
}

/* Food row */
.food-row {
  display: flex;
  align-items: flex-start;
  gap: 10px;
  padding: 8px 10px;
  background: var(--cream);
  border-radius: 10px;
  border: 1px solid var(--border);
  animation: slideUp 0.15s ease;
}

.food-row-icon {
  font-size: 22px;
  flex-shrink: 0;
  line-height: 1;
  margin-top: 2px;
}

.food-row-info { flex: 1; min-width: 0; }

.food-row-name {
  font-size: 13px;
  font-weight: 600;
  color: var(--brown);
  margin-bottom: 5px;
  line-height: 1.3;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.food-row-chips {
  display: flex;
  flex-wrap: wrap;
  gap: 4px;
}

.food-remove-btn {
  background: none;
  border: none;
  color: var(--muted);
  font-size: 18px;
  line-height: 1;
  padding: 0 2px;
  border-radius: 4px;
  transition: all 0.15s;
  flex-shrink: 0;
  margin-top: 2px;
}

.food-remove-btn:hover {
  color: var(--red);
  background: #FEE;
}

/* Add button */
.add-food-btn {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 6px;
  width: 100%;
  padding: 9px;
  border: 1.5px dashed var(--border);
  border-radius: 10px;
  background: none;
  color: var(--muted);
  font-size: 13px;
  font-weight: 500;
  transition: all 0.18s;
  margin-top: 4px;
}

.add-food-btn:hover {
  border-color: var(--accent);
  color: var(--accent);
  background: #FFF5EF;
}

.add-icon { font-size: 16px; font-weight: 300; }