.sidebar {
  background: var(--brown);
  border-left: 1px solid rgba(255,255,255,0.06);
  overflow-y: auto;
  position: sticky;
  top: 62px;
  max-height: calc(100vh - 62px);
}

.sidebar-inner {
  padding: 24px 20px 32px;
  display: flex;
  flex-direction: column;
  gap: 20px;
}

.sidebar-title {
  font-family: var(--font-display);
  font-size: 20px;
  color: var(--cream);
  margin-bottom: -12px;
}

.sidebar-day {
  font-size: 12px;
  color: rgba(250,247,242,0.4);
  text-transform: uppercase;
  letter-spacing: 0.7px;
  font-weight: 600;
}

/* Ring */
.kcal-ring-wrap {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
}

.kcal-ring-svg {
  width: 130px;
  height: 130px;
}

.kcal-ring-center {
  position: absolute;
  text-align: center;
}

.kcal-ring-num {
  font-family: var(--font-display);
  font-size: 30px;
  font-weight: 700;
  line-height: 1;
  transition: color 0.3s;
}

.kcal-ring-label {
  font-size: 11px;
  color: rgba(250,247,242,0.5);
  margin-top: 2px;
}

.kcal-ring-goal {
  font-size: 11px;
  color: rgba(250,247,242,0.35);
}

/* Macro bars */
.macro-bars {
  display: flex;
  flex-direction: column;
  gap: 12px;
}

.macro-bar-row { display: flex; flex-direction: column; gap: 5px; }

.macro-bar-top {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.macro-bar-name { font-size: 13px; font-weight: 500; color: var(--cream); }
.macro-bar-val  { font-size: 12px; color: rgba(250,247,242,0.55); }
.macro-bar-val.over { color: var(--red) !important; font-weight: 700; }

.macro-track {
  height: 5px;
  background: rgba(255,255,255,0.1);
  border-radius: 3px;
  overflow: hidden;
}

.macro-fill {
  height: 100%;
  border-radius: 3px;
  transition: width 0.5s ease;
}

/* Tiles */
.nutrient-tiles {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 8px;
}

.nutrient-tile {
  background: rgba(255,255,255,0.05);
  border: 1px solid rgba(255,255,255,0.08);
  border-radius: 10px;
  padding: 10px 12px;
  display: flex;
  flex-direction: column;
  gap: 2px;
}

.nutrient-tile-icon  { font-size: 16px; }
.nutrient-tile-val   { font-size: 18px; font-weight: 700; font-family: var(--font-display); line-height: 1.2; }
.nutrient-tile-unit  { font-size: 10px; color: rgba(250,247,242,0.4); }
.nutrient-tile-label { font-size: 11px; color: rgba(250,247,242,0.5); margin-top: 2px; }

/* Tips */
.sidebar-tips {
  background: rgba(200,96,42,0.14);
  border: 1px solid rgba(200,96,42,0.28);
  border-radius: 12px;
  padding: 14px 14px;
}

.tips-title {
  font-size: 11px;
  font-weight: 700;
  text-transform: uppercase;
  letter-spacing: 0.6px;
  color: var(--accent2);
  margin-bottom: 8px;
}

.tip-item {
  font-size: 12px;
  color: rgba(250,247,242,0.7);
  padding-left: 12px;
  position: relative;
  margin-bottom: 5px;
  line-height: 1.5;
}

.tip-item::before {
  content: '→';
  position: absolute;
  left: 0;
  color: var(--accent2);
}