.weekly-chart {
  background: var(--card);
  border: 1.5px solid var(--border);
  border-radius: 16px;
  padding: 20px 24px;
}

.chart-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 20px;
}

.chart-title {
  font-family: var(--font-display);
  font-size: 17px;
  color: var(--brown);
}

.chart-legend {
  display: flex;
  align-items: center;
  gap: 6px;
  font-size: 12px;
  color: var(--muted);
}

.legend-dot {
  display: inline-block;
  width: 8px;
  height: 8px;
  border-radius: 50%;
}

.chart-bars {
  display: grid;
  grid-template-columns: repeat(7, 1fr);
  gap: 8px;
  align-items: end;
}

.chart-col {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 5px;
}

.bar-wrap {
  width: 100%;
  height: 80px;
  position: relative;
  display: flex;
  align-items: flex-end;
}

.bar-fill {
  width: 100%;
  border-radius: 6px 6px 0 0;
  transition: height 0.4s ease;
  min-height: 3px;
}

.goal-line {
  position: absolute;
  left: -2px;
  right: -2px;
  height: 2px;
  background: var(--accent);
  opacity: 0.35;
  border-radius: 1px;
}

.bar-label {
  font-size: 11px;
  font-weight: 700;
  color: var(--muted);
  text-transform: uppercase;
  letter-spacing: 0.5px;
}

.bar-kcal {
  font-size: 11px;
  color: var(--muted);
  font-weight: 500;
}

.chart-col.active .bar-label { color: var(--accent); }
.chart-col.active .bar-kcal  { color: var(--brown); font-weight: 700; }