@import url('https://fonts.googleapis.com/css2?family=Playfair+Display:wght@400;600;700&family=DM+Sans:wght@300;400;500;600&display=swap');

:root {
  --cream:   #FAF7F2;
  --warm:    #F0E8D8;
  --brown:   #3D2B1F;
  --brown2:  #5C3D2A;
  --accent:  #C8602A;
  --accent2: #E07840;
  --green:   #4A7C59;
  --yellow:  #C9952A;
  --red:     #C0392B;
  --muted:   #9A8F84;
  --border:  #E8DDD0;
  --card:    #FFFCF8;
  --shadow:  rgba(61,43,31,0.10);
  --font-display: 'Playfair Display', Georgia, serif;
  --font-body:    'DM Sans', sans-serif;
}

*, *::before, *::after { box-sizing: border-box; margin: 0; padding: 0; }

body {
  font-family: var(--font-body);
  background: var(--cream);
  color: var(--brown);
  min-height: 100vh;
  -webkit-font-smoothing: antialiased;
}

button { font-family: var(--font-body); cursor: pointer; }
input  { font-family: var(--font-body); }

/* ── ROOT LAYOUT ── */
.app-root { display: flex; flex-direction: column; min-height: 100vh; }

.app-body {
  display: grid;
  grid-template-columns: 1fr 300px;
  flex: 1;
  min-height: 0;
}

.app-main {
  padding: 28px 32px 40px 36px;
  overflow-y: auto;
  display: flex;
  flex-direction: column;
  gap: 24px;
}

/* ── MODAL OVERLAY ── */
.modal-overlay {
  position: fixed;
  inset: 0;
  background: rgba(30,18,10,0.55);
  backdrop-filter: blur(4px);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 200;
  animation: fadeIn 0.15s ease;
}

@keyframes fadeIn { from { opacity: 0; } to { opacity: 1; } }
@keyframes slideUp { from { transform: translateY(16px); opacity: 0; } to { transform: translateY(0); opacity: 1; } }

.modal-box {
  background: var(--card);
  border-radius: 20px;
  box-shadow: 0 24px 64px rgba(0,0,0,0.28);
  animation: slideUp 0.2s ease;
  overflow: hidden;
}

/* ── SHARED BUTTON STYLES ── */
.btn {
  display: inline-flex;
  align-items: center;
  gap: 6px;
  padding: 9px 18px;
  border-radius: 10px;
  border: none;
  font-size: 13px;
  font-weight: 600;
  transition: all 0.18s ease;
  white-space: nowrap;
}
.btn-accent  { background: var(--accent); color: #fff; }
.btn-accent:hover  { background: var(--accent2); transform: translateY(-1px); box-shadow: 0 4px 14px rgba(200,96,42,0.35); }
.btn-ghost   { background: transparent; border: 1.5px solid var(--border); color: var(--brown2); }
.btn-ghost:hover   { border-color: var(--accent); color: var(--accent); background: #FFF5EF; }
.btn-danger  { background: #FEE; color: var(--red); border: 1.5px solid #FCCACA; }
.btn-danger:hover  { background: var(--red); color: #fff; }
.btn-dark    { background: var(--brown); color: var(--cream); }
.btn-dark:hover    { background: var(--brown2); }

/* ── PILL / CHIP ── */
.chip {
  display: inline-flex;
  align-items: center;
  gap: 4px;
  padding: 3px 10px;
  border-radius: 14px;
  font-size: 11px;
  font-weight: 600;
}
.chip-p { background: #E8F5E9; color: #2E7D32; }
.chip-c { background: #FFF8E1; color: #E65100; }
.chip-g { background: #F3E5F5; color: #6A1B9A; }
.chip-k { background: #FBE9E7; color: var(--accent); }

/* ── SCROLLBAR ── */
::-webkit-scrollbar { width: 5px; height: 5px; }
::-webkit-scrollbar-track { background: transparent; }
::-webkit-scrollbar-thumb { background: var(--border); border-radius: 4px; }
::-webkit-scrollbar-thumb:hover { background: var(--muted); }

@media (max-width: 900px) {
  .app-body { grid-template-columns: 1fr; }
  .app-main { padding: 20px 16px 32px; }
}