.goal-modal {
  width: 420px;
  padding: 28px;
  display: flex;
  flex-direction: column;
  gap: 20px;
}

.goal-modal-head h2 {
  font-family: var(--font-display);
  font-size: 22px;
  margin-bottom: 4px;
}

.goal-modal-head p {
  font-size: 13px;
  color: var(--muted);
}

/* Presets */
.goal-presets {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 8px;
}

.preset-btn {
  padding: 9px 10px;
  border-radius: 10px;
  border: 1.5px solid var(--border);
  background: var(--cream);
  font-size: 12px;
  font-weight: 600;
  color: var(--brown);
  transition: all 0.15s;
}
.preset-btn:hover {
  border-color: var(--accent);
  background: #FFF5EF;
  color: var(--accent);
}

/* Fields */
.goal-fields {
  display: flex;
  flex-direction: column;
  gap: 14px;
}

.goal-field {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 16px;
}

.goal-field-label {
  font-size: 14px;
  font-weight: 600;
  color: var(--brown);
}

.goal-field-input-wrap {
  display: flex;
  align-items: center;
  gap: 6px;
}

.goal-input {
  width: 90px;
  padding: 8px 12px;
  border-radius: 8px;
  border: 1.5px solid var(--border);
  font-size: 16px;
  font-weight: 700;
  text-align: right;
  color: var(--brown);
  background: var(--cream);
  font-family: var(--font-display);
  outline: none;
  transition: border-color 0.2s;
}
.goal-input:focus { border-color: var(--accent); }

.goal-unit {
  font-size: 13px;
  color: var(--muted);
  width: 28px;
}

/* Footer */
.goal-modal-footer {
  display: flex;
  justify-content: flex-end;
  gap: 10px;
  padding-top: 4px;
  border-top: 1px solid var(--border);
}